/* Define :root variables if needed, or handle themes via JS/Context */
body {
    margin: 0;
    font-family: 'Helvetica Neue', sans-serif;
    background-color: #f8f9fa; /* Light mode default */
    color: #212529;
  }
  
  body.dark-mode {
    background-color: #121212;
    color: #e9ecef;
  }
  
  /* Add other global resets or base styles */
  *, *::before, *::after {
      box-sizing: border-box;
  }